import { Component, OnInit, Input, EventEmitter } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';

import { standarFlow, StandarApprovalService } from './standar-approval.service';
import { Service_data_flow } from 'src/app/sdk/user/models';
import { ToolsServiceDataFlowService } from 'src/app/sdk/user/services/tools-service-data-flow.service';


@Component({
    selector: 'erp-approval-standar',
    templateUrl: './standar.component.html',
    styleUrls: ['./standar.component.scss']
})
export class StandarComponent implements OnInit {


    constructor(
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
        private toolsServiceDataFlowService: ToolsServiceDataFlowService,
        private standarApprovalService: StandarApprovalService,
    ) { }

    param: standarFlow = null;

    ngOnInit() {
        this.standarApprovalService.getId().subscribe(
            data => {
                this.param = data;
                if (data) {

                    if (data.id_data_flow_detail) //klo null artinyo lah close
                    {
                        this.getDataFlow()
                    }

                }
            }
        )
    }

    dataFlow: any = {}
    getDataFlow() {
        this.spinner.show()
        this.toolsServiceDataFlowService.getToolsServiceDataFlows(JSON.stringify(this.param)).subscribe(
            data => {
                this.spinner.hide()
                this.dataFlow = data.data;
            }
        )
    }

    confirm(status) {
        this.spinner.show()
        this.param.next_status = status;
        this.toolsServiceDataFlowService.postToolsServiceDataFlows(this.param).subscribe(
            data => {
                this.spinner.hide()
                this.standarApprovalService.isreload.next(true)
            }
        )
    }

    delete() {
        this.spinner.show()
        this.param.next_status = status;
        if (this.dataFlow['data_flow_detail']['order_by'] > 1) {
            this.pesan.pesanWarningForm('Dokumen Ini tidak bisa di delete!');
            return false;
        }
        this.toolsServiceDataFlowService.deleteToolsServiceDataFlowsId(this.param.id).subscribe(
            data => {
                this.spinner.hide()
                this.standarApprovalService.isreload.next(true)
            },
            err => {
                this.pesan.pesanWarningForm('Dokumen Ini tidak bisa di delete!');
                return false;
            }
        )
    }


}
