import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PelamarServiceService {

    constructor() {}

    id_pelamar = new BehaviorSubject<string>(null)
    setIdpelamar(id_pelamar: string) {
        this.id_pelamar.next(id_pelamar)
    }

    getIdPelamar() {
        return this.id_pelamar.asObservable()
    }
}
