import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ManufatureServiceService {

  constructor() { }

  id_manufacture = new BehaviorSubject<string>(null)
  setIdManufacture(id_manufacture: string) {
      this.id_manufacture.next(id_manufacture)
  }

  getIdManufacture() {
      return this.id_manufacture.asObservable()
  }
}
