import { BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class ModelDetailService {

    constructor() { }

    idModel = new BehaviorSubject<string>(null);
    getIdModel() {
        return this.idModel.asObservable()
    }

    setIdModel(id_model: string) {
        this.idModel.next(id_model)
    }

}
