import { Injectable } from '@angular/core'
import { BehaviorSubject } from 'rxjs'

@Injectable({
    providedIn: 'root',
})
export class PegawaiDetailService {
    constructor() {}

    id_pegawai = new BehaviorSubject<string>(null)
    setIdpegawai(id_pegawai: string) {
        this.id_pegawai.next(id_pegawai)
    }

    getIdPegawai() {
        return this.id_pegawai.asObservable()
    }
}
