export interface standarFlow {
    id: string,
    id_data_flow: string,
    id_data_flow_detail: string,
    next_status: string,
    primary_key: string,
}


import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})

export class StandarApprovalService {

    constructor() { }

    // @Input() id = new EventEmitter<string>();
    // @Input() id_data_flow: string;
    // @Input() id_data_flow_detail: string;
    // @Input() next_status: string;



    isreload = new BehaviorSubject<boolean>(false);

    param = new BehaviorSubject<standarFlow>(null);
    getId() {
        return this.param.asObservable();
    }
    getisreload() {
        return this.isreload.asObservable();
    }

}
